.Schedule .stationName h1{
    padding: 0 15px;
    margin: 10px 0;
    font-size: 20px;
    width: 100%;
}

.Schedule .timeTable{
    overflow: auto;
}

.Schedule .timeTable ul{
    list-style-type:none;
    margin: auto;
    padding: 0;
    width: 100%;
}

.Schedule .timeTable ul li{
    padding: 10px;
    margin: 3px 0px;
    margin-left: 20px;
    display: -ms-flexbox;
    display: flex;
    width: 90%;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    border-radius:10px;
}

.Schedule .timeTable ul li:hover{
    background-color: #747474;
    color:white;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
    -webkit-transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
}

.Schedule .timeTable ul li:active, .Schedule .timeTable ul li:focus{
    background-color: #3b3b3b;
}

.Schedule .timeTable ul li p{
    margin: auto;
    text-align: center;
    font-size: 20px;
}

.preload{
    height: auto;
    margin: auto;
}

.error{
    margin: 0 auto;
    padding: 50px 0;
    font-size: 30px;
    text-align: center;
}
.error h2{
    margin: 0;

}



@-webkit-keyframes spinner {
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
  }



@keyframes spinner {
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
  }
   
  .spinner:before {
    content: '';
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30px;
    height: 30px;
    margin-top: -15px;
    margin-left: -15px;
    border-radius: 50%;
    border: 1px solid #ccc;
    border-top-color: #46C694;
    -webkit-animation: spinner .6s linear infinite;
            animation: spinner .6s linear infinite;
  }




